	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/04/crh0419.jpg\" height=\"414\" width=\"350\" border=\"0\" alt=\"crh0419.jpg: \" /></p>\n<p>Here is Christopher just waking up this morning. (April 23: Christopher says, \"Thanks for the <a href=\"http://scriptingnews.userland.com/backissues/2000/04/23\">link</a>, Dave. I'll take that as a compliment!\") Points of note:</p>\n<ul>\n<li>He's wearing his Oshkosh locomotive pyjamas, from John and Diane\n</li>\n<li>He's kicked the covers off, as usual\n</li>\n<li>He's not smiling, because he sees the camera\n</li>\n<li>He's lying crooked, with feet slightly to the left. He`d turn completely to the left if he could.\n</li>\n<li>Be thankful that <a href=\"http://www.digiscents.com\">DigiScents</a> is not activated for this page</li>\n</ul>\n<p><a href=\"http://Mama.editthispage.com\">Mama</a> has a picture of a <a href=\"http://Mama.editthispage.com/2000/04/19\">complete left turn</a>. He just slides under the side of the gym.</p>","fields":{"title":"Waking up","date":"19 Apr 2000","url":"https://papascott.de/2000-04-19-waking-up/"}}},"pathContext":{"slug":"/2000-04-19-waking-up/","prev":{"fields":{"title":"Back to the old routine","date":"17 Apr 2000","slug":"/2000-04-17-back-to-the-old-routine/"}},"next":{"fields":{"title":"Easter Sunday","date":"23 Apr 2000","slug":"/2000-04-23-easter-sunday/"}}}}

